@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.index {
  padding-top: rem(32);
  padding-bottom: rem(32 - 16);

  font-size: rem(16);
  line-height: rem(24);
  text-align: center;
  @include var (font-family,--font-heading);
  font-weight: 200;
  
  @include media-breakpoint-up(md) {

    font-size: rem(20);
    line-height: rem(24);
  }

  .col {
    border-right: 1px solid currentColor;
    @include var(border-color, --colour-border);
    margin-bottom: rem(16);
    min-width: calc(100%/7);
    max-width: calc(100%/7);

    &:nth-child(7n),
    &:last-child {
      border-right: none;
    }

    @include media-breakpoint-up(sm) {

      min-width: calc(100%/13);
      max-width: calc(100%/13);

      &:nth-child(7n)  {
        border-right: 1px solid currentColor;
        @include var(border-color, --colour-border);
      }
      &:nth-child(13) {
        border-right: none;
      }
    }

    @include media-breakpoint-up(md) {

      min-width: calc(100%/26);
      max-width: calc(100%/26);

      &:nth-child(13)  {
        border-right: 1px solid currentColor;
        @include var(border-color, --colour-border);
      }
    }
  }

  a {
    font-weight: 700;
    display: block;
  }
}