@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

// #region card
.card {

  padding-bottom: rem(24 - 8);

  @include media-breakpoint-up(sm) {
    
    padding-bottom: rem(32 - 8);
  }

  position: relative;
  display: block;
  //flex-direction: column;
  margin-bottom: rem(16);
  @include var(background-color, --colour-white);
  text-align: left;
  transition: box-shadow 0.2s ease-in-out;
  box-shadow: 0 0.25rem 1rem 0 rgba(0, 0, 0, 0.08);

  &:hover:not(:active),
  &:focus:not(:active),
  &:active {
    text-decoration: none;
    box-shadow: 0 0.5rem 1.1rem 0 rgba(0, 0, 0, 0.25);

    .card-image img{
      opacity: 0.8;
    }

    .card-body *:not(.card-title) {
      @include var(color, --colour-body);
    }
    .card-body .card-title {
      @include var(color, --colour-hover);
    }

    .btn-link:after {
      border-bottom-width: 2px;
      margin-bottom: -1px;
    }
  }

  &:hover:not(:active),
  &:focus:not(:active) {
    
    .card-body .card-title,
    .btn-link {
      @include var(color, --colour-hover);
    }
  }

  &:active {
    
    .card-body .card-title,
    .btn-link {
      @include var(color, --colour-active);
    }
  }

  @include media-breakpoint-up(sm) {
    display: flex;
    flex-flow: column;
  }

  .card-icon {
    display: none;
  }

  .youtube-embed__btn {

    @include media-breakpoint-up(sm) {
  
      width: rem(120);
      height: rem(120);
  
      .svg__wrapper {
        height: rem(120 - 16);
        width: rem(120 - 16);
  
        svg {
  
          stroke-width: 0.75px;
        }
      }
    }
  }
}
// #endregion

// #region Card body
.card-body {
  padding: rem(24) rem(24) rem(8) rem(24);

  @include media-breakpoint-up(sm) {
    
    padding: rem(32) rem(32) rem(8) rem(32);

  }

  > * {
    padding-bottom: rem(16);
  }

  @if $compatible == 'true' {
    h1, h2,h3  {
      @include var(color,--colour-body,!important);
    }
  }

  
    @if $compatible == 'true' {
      &:hover:not(:active),
      &:focus:not(:active),
      &:active  {
        h1, h2, h3  {
          @include var(color,--colour-primary,!important);
        }
    }
  }
}
// #endregion

// #region Card image 
.card-image {
  padding-top: 56.25%;
  position: relative;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 100%;
    height: 100%;
    min-height: 100%;
    min-width: 100%;
    object-fit: cover;
    transform: translate(-50%, -50%);
    margin: 0;
  }

  .badge {
    position: absolute;
    bottom: 0;
    left: 0;
    z-index: 1;
    margin: 0;

    &:empty {
      display: none;
    }
  }
}
// #endregion

// #region Card footer
.card-footer {
  padding: 0 rem(24) rem(8) rem(24);
  margin-top: auto;

  @include media-breakpoint-up(sm) {
    
    padding: 0 rem(32) rem(8) rem(32);
  }
  .card-author {
    display: none;
  }
  .btn-link {
    margin-bottom: 0;
  }
}
// #endregion
