@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

/* Notfication */
.notification {

  display: flex;
  flex-flow: row wrap;
  position: relative;
  text-align: left!important;
  padding-top: rem(16);
  padding-bottom: 0;

  
  @media (max-width: 36em) {
    
    padding-left: rem(16);
    padding-right: rem(16);
  }
  
  .notification__icon {
    
    min-width: rem(24 + 16);
    max-width: rem(24 + 16);

    @include media-breakpoint-up(sm) {
      width: rem(80);
    }

    &.svg__wrapper {

      svg {
        width: rem(24);
        height: rem(24);    
        vertical-align: text-bottom;
      }
    }
  }

  .notification__title {
    flex: 1 1 calc(100% - #{rem(24 + 16 + 24 + 16)});
    padding-bottom: rem(16);
    
    @include media-breakpoint-up(md) {
      flex: 1 1 calc(50% - #{rem(24 + 16 + 24 + 16)});
    }
  }

  .notification__action {

    flex: 1 1 auto;
    min-width: 100%;
    padding-bottom: rem(13);
    margin-top: rem(-2);

    .btn-link {
      
      margin-bottom: -1px;

    }

    @include media-breakpoint-up(md) {
      min-width: 0;
      text-align: right;
    }
  }

  .notification__icon ~ .notification__action {
    padding-left: rem(24 + 16);
  }

  .notification__close {
    
    flex: 1 1 auto;
    text-align: right;

    button {
      background: none;
      border: none;
      padding: 0;

      &:hover,
      &:focus {
        @include var(color,--colour-hover);
      }

      &:active  {
        @include var(color,--colour-active);
      }
    }
    
    .svg__wrapper {
      display: block;
  
      svg {
        width: rem(24);
        height: rem(24);
        vertical-align: text-bottom;
      }
    }
  }

}
