@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.calculator {

  &__results {
    @include var(background,--colour-white);
    padding: rem(24) rem(16);
    margin-left: rem(-16);
    margin-right: rem(-16);
    margin-bottom: rem(32);
    box-shadow: 0 4px 16px 0 rgba(0,0,0,0.08);

    @include media-breakpoint-up(sm) {
      padding: rem(32) rem(24);
      margin-left: rem(-24);
      margin-right: rem(-24);
    }
    @include media-breakpoint-up(md) {
      padding: rem(32);
      margin-left: 0;
      margin-right: 0;
    }

    > .btn-group:last-child {
      padding-bottom: 0;

      > .btn:last-child {
        margin-bottom: 0;
      }
    }
  }
}