@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

// #region carousel
.article-deck.carousel {
  display: flex;
  display: inline-flex;
  flex-flow: row nowrap;
  margin-left: rem(-16);
  margin-right: rem(-16);
  padding-bottom: 0;
  padding-right: rem(24);

  .card {
    margin: 0 rem(8) rem(32) rem(8);
    flex-shrink: 0;
    flex-grow: 0;
    min-width: rem(375 - 32 - 32 - 16);
    flex-basis: rem(375 - 32 - 32 - 16);
    scroll-snap-align: start;
  }

  @include media-breakpoint-up(sm) {

    .card {
      scroll-snap-align: unset;

      min-width: rem(384 - 48 - 16);
      flex-basis: rem(384 - 48 - 16);

      &:last-child:nth-child(odd){
        margin-right: rem(-8);
      }
    }

    .card:nth-child(odd) {
      scroll-snap-align: start;
    }
  }
  @include media-breakpoint-up(md) {
    
    padding-right: rem(32);

    .card {
      scroll-snap-align: unset;

      min-width: rem(372.8);
      flex: 0 0;

      margin-left: rem(16);
      margin-right: rem(16);
      box-sizing: border-box;
      &:last-child:nth-child(odd){
        margin-right: rem(16);
      }
      &:last-child:not(:nth-child(3n)){
        margin-right: rem(-16);
      }
    }

    .card:nth-child(odd) {
      scroll-snap-align: unset;
    }

    .card:nth-child(3n + 1) {
      scroll-snap-align: start;
    }
  }

  @media (min-width: #{em(1440)}) {
    .card {
      min-width: rem(378.66667);
    }
  }
}


.carousel__wrapper {
  padding-left: rem(32 + 8);
  padding-right: rem(32);
  margin-left: rem(-32);
  margin-right: rem(-32);
  overflow: auto;
  scroll-snap-type: x mandatory;
  scroll-padding: rem(32);

  @include media-breakpoint-up(sm) {


    padding-left: rem(48 + 8);
    padding-right: rem(48);
    margin-left: rem(-48);
    margin-right: rem(-48);
    scroll-padding: rem(48);
    min-height: 2px; // used by the javascript
  }  

  @include media-breakpoint-up(md) {

    padding-left: #{$container-md-padding};
    padding-right: #{$container-md-padding};
    margin-left: -#{$container-md-padding};
    margin-right: -#{$container-md-padding};
    scroll-padding: #{$container-md-padding};
    min-height: 3px; // used by the javascript
  }
}
// #endregion

// #region Javascript controls
.carousel__controls{

  margin-left: rem(-32);
  margin-right: rem(-32);

  @include media-breakpoint-up(sm) {

    margin-left: rem(-48);
    margin-right: rem(-48);
  }
  overflow: hidden;


  
  .control-left,
  .control-right {
    margin-bottom: 0;
  }
  
  .control-right {
    float: right;
  }

  @include media-breakpoint-up(md) {

    position: static;

    .control-left,
    .control-right {
      position: absolute;
      top: 50%;
      left: 0;
      z-index: 99;
      margin-top: rem(-33);
    }
    .control-right {
      left: auto;
      right: 0;
    }
  }
}
// #endregion

// #region Pips
body.js-enabled .carousel__controls .pips {
  position: absolute;
  width: 50%;
  left: 25%;
  z-index: 2;

  .pip:nth-child(6) ~ .pip {
    display: none;
  }

  @include media-breakpoint-up(sm) {

    .pip:nth-child(6) ~ .pip:not(:nth-child(even)) {
      display: inline-block;
    }
    .pip:nth-child(even){
      display: none;
    }
  }

  @include media-breakpoint-up(md) {

    padding-top: 0;

    .pip:nth-child(6) ~ .pip:nth-child(3n+1),
    .pip:nth-child(even){
      display: inline-block;
    }

    .pip:nth-child(6) ~ .pip:not(:nth-child(3n+1)),
    .pip:not(:nth-child(3n+1)){
      display: none;
    }
  }
}

// #endregion


// #region Mask sides for desktop
@include media-breakpoint-up(md) {
  .carousel__container {

    position: relative;
    color: var(--bg-colour,#ffffff)!important;
    margin-left: -#{$container-md-padding};
    padding-left: #{$container-md-padding};
    margin-right: -#{$container-md-padding};
    padding-right: #{$container-md-padding};

  }
  
  // Added to control the overflow
  .carousel__mask {

    position: relative;
    color: var(--bg-colour,#ffffff)!important;
    margin-left: -#{rem(20)};
    padding-left: #{rem(20)};
    margin-right: -#{rem(20)};
    padding-right: #{rem(20)};
    overflow: hidden;
  }
  
  // Add margin bottom to allow for space for pips
  body.js-enabled {

    .carousel__container { 
      margin-bottom: rem(48);
    }
  }
}
// #endregion


// #region Hide scrollbar
body.js-enabled .carousel__wrapper {
  // FireFox
  scrollbar-width: none;
  // IE10+
  -ms-overflow-style: none;
}
body.js-enabled .carousel__wrapper::-webkit-scrollbar {
  // Chrome, Safari, Edge
  display: none;
}
// #endregion
