@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

// #region Card Deck
@include media-breakpoint-up(md) {
  .container--deck:not(.container--deck-full-width) {
    padding: rem(80) rem(192) rem(64) rem(192);
  }
}

.card-deck {
  
  @include media-breakpoint-up(sm) {
    margin-left: rem(-16);
    margin-right: rem(-16);
    display: flex;
    flex-flow: row wrap;
    justify-content: center;

    .card {
      margin: 0 rem(16) rem(32) rem(16);
      width: calc(50% - #{rem(16 + 16)});
    }
  }
}
// #endregion

// #region Card deck icon
.card-deck--icon {
  .card {
    padding-left: rem(32);

    .card-icon {
      display: block;
      position: absolute;
      top: rem(24 + 4);
      left: rem(16);
      width: rem(32);
      height: rem(32);
      margin-left: 0;

      @include media-breakpoint-up(sm) {

        top: rem(32 + 4);
      }

      svg {
        width: rem(32);
        height: rem(32);
        @include var(stroke,--colour-primary)
      }
    }
  }
}
// #endregion

// #region Card Deck icon large
.card-deck--icon-large {
  .card {

    .card-title {
      min-height: rem(80 + 24);
      padding-right: rem(64);
      @include var(color,--colour-primary);
    }

    .card-icon {
      display: block;
      position: absolute;
      top: rem(24);
      right: rem(16);
      width: rem(80);
      height: rem(80);
      margin-left: 0;

      @include media-breakpoint-up(sm) {

        top: rem(32);
      }

      svg {
        width: 100%;
        height: 100%;
        stroke: $colour-body;
        
      }
    }
  }
}
// #endregion

// #region Card deck image PORTRAITc

.card-deck--portrait {
  .card {
    padding-left: rem(120);

    .card-image {
      height: 100%;
      padding-top: 0;
      width: rem(120);
      position: absolute;
      top: 0;
      left: 0;
    }

    &:nth-child(even) {
      padding-left: 0;
      padding-right: rem(120);

      .card-image {
        left: auto;
        right: 0;
      }
    }

    @include media-breakpoint-up(sm) {
      padding-left: rem(150);
      width: 100%;

      .card-image {
        width: rem(150);
      }
    }
  }

  @include media-breakpoint-up(md) {
    &:not(.card-deck--portrait-single) .card {
    
      width: calc(50% - 2rem);

      &:nth-child(even) {
        padding-right: 0;
        padding-left: rem(150);

        .card-image {
          left: 0;
          right: auto;
        }
      }

      &:nth-child(4n - 2) + .card,
      &:nth-child(4n - 2) + .card + .card {
        padding-left: 0;
        padding-right: rem(150);

        .card-image {
          left: auto;
          right: 0;
        }
      }
    }
  }
}
// #endregion


// #region Card deck btn fix
.card-deck ~ .btn,
.card-deck ~ div > .btn {

  margin: 0 auto;

  @include media-breakpoint-up(sm) {
    
    margin: 0 auto rem(32) auto;
  }
}
// #endregion

// #region 3 columns
@include media-breakpoint-up(md) {
  .card-deck--3-cols {

    .card {
      width: calc(33.33333% - #{rem(32)});
    }
  }
}
// #endregion