@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.pagination {

  &:before,
  &:after {
    display: none;
  }

  display: flex;
  flex-flow: row wrap;
  padding: rem(16) 0 rem(48) 0;

  @include media-breakpoint-up(sm) {

    flex-flow: row nowrap;
    padding-top: rem(32);
    padding-bottom: rem(64);
  }


  @include media-breakpoint-up(md) {

    padding-top: rem(40);
    padding-bottom: rem(80);

    @include media-breakpoint-up(md) {

      padding: rem(40) $container-md-padding rem(80) $container-md-padding;
    }
  }

  &.container[class*="bg-"] {

    background: none!important;
    --bg-pagination-next: var(--bg-colour);
    --bg-pagination-prev: var(--bg-colour-tint);
    --colour-pagination: initial;
  }

  > a {
    color: var(--colour-pagination);
    flex: 0 0 100%;
    padding: rem(32) rem(32) rem(32) rem(32);

    @include media-breakpoint-up(sm) {

      flex: 1 0 50%;
    }

    .body {
      display: block;
      text-transform: uppercase;
      padding-bottom: rem(16);
      text-decoration: none;
    }

    .h4 {
      padding-bottom: rem(16);
      color: inherit;
    }

    &:hover,
    &:focus,
    &:active {

      color: var(--colour-pagination);
      text-decoration: none;

      .body {
        text-decoration: none;
      }

      .h4 {
        text-decoration: underline;
      }
    }
    
    @include media-breakpoint-up(sm) {

      padding: rem(32) rem(24);

      &:last-child {
        padding-left: rem(48);
      }
      &:first-child {
        padding-right: rem(48);
      }
    }
    @include media-breakpoint-up(md) {

      padding: rem(32);

      &:last-child {
        padding-left: rem(32);
      }
      &:first-child {
        padding-right: rem(32);
      }
    }
  }

  &__next {
    background: var(--bg-pagination-next);

    @include media-breakpoint-up(sm) {
      order: 2;
    }
  }

  &__previous {
    background: var(--bg-pagination-prev);
  }

  @if $compatible == 'true' {

    &:not([class*="bg-"]){
      
      .pagination__next {
        @include var(background,--colour-secondary,!important);
        color: #ffffff;
      }
      .pagination__previous {
        @include var(background,--colour-secondary-light,!important);
        color: #ffffff;
      }

      > a {
        &:hover,
        &:focus,
        &:active {

          @include var(background,--colour-secondary-tint,!important);
          color: #ffffff;
        }
      }
    }

    @each $color, $value in $theme-colors {
      &.bg-#{$color}{
        .pagination__next {
          @include var(background,--colour-#{str-replace($color,'.bg-','')},!important);
        }
        .pagination__previous {
          @include var(background,--colour-#{str-replace($color,'.bg-','')}-light,!important);
        }

        > a {
          &:hover,
          &:focus,
          &:active {

            @include var(background,--colour-#{str-replace($color,'.bg-','')}-tint,!important);
          }
        }
      }
    }
  }
}

.container .pagination.container { 

  padding-top: rem(16);
  padding-bottom: rem(48);
  margin-left: -#{$container-padding};
  margin-right: -#{$container-padding};
  width: auto;

  @include media-breakpoint-up(sm) {

    padding-top: rem(32);
    padding-bottom: rem(64);
    margin-left: -#{$container-sm-padding};
    margin-right: -#{$container-sm-padding};
  }

  @include media-breakpoint-up(md) {

    padding-top: rem(40);
    padding-bottom: rem(80);
    margin-left: 0;
    margin-right: 0;
  }
}