@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";


.tabs ul {

  list-style: none;
  padding: 0;
  margin: 0 -#{$container-padding} 0 -#{$container-padding};
  justify-content: center;
  max-width: none;


  .tab {
    display: block;
    padding: 0;
  
    &:before {
      content: '';
    }
  }

  .tab__target {
    display: block;
    text-align: left;
    padding: rem(20) rem(16) rem(20) rem(16);
    text-decoration: none;
    border-bottom: 1px solid currentColor;
    @include var(border-color,--colour-border);
  
    &:before {
      height: 100%;
      width: rem(4);
      background: transparent;
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      display: block;
      transform: none;    }
  
    &:active,
    &:hover,
    &:focus {
      text-decoration: none;
    }

    &:hover,
    &:focus {
      @include var(background,--colour-light);
      @include var(color,--colour-body);
    }
  
    &:active {
      @include var(background,--colour-light);
      @include var(color,--colour-active);
    }
  
    &:hover:before,
    &:focus:before,
    &:active:before,
    .js-enabled &:not(.collapsed):before {
      
      background: currentColor;
    }
  }
  
  .js-enabled & a[role="tab"]:not(.collapsed) {
  
    @include var(color,--colour-primary);
    pointer-events: none; // If tab is already open prevent the user from closing it
  }

  @include media-breakpoint-up(sm) {
    display: flex;
    flex-wrap: nowrap;
    border-bottom: 1px solid currentColor;
    @include var(border-color,--colour-border);
    margin-left: auto;
    margin-right: auto;
    -webkit-box-pack: center!important;
    -ms-flex-pack: center!important;
    justify-content: center!important;

    .tab {
      border: 0;
      width: auto;
      min-width: calc(100% / 6);
      max-width: rem(320);
      flex: 0 1 rem(320);
    }

    .tab__target {
      border: none;
      text-align: center;
      justify-content: center;
      -webkit-box-align: end;
      -ms-flex-align: end;
      align-items: flex-end;
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      min-height: 100%;
      padding: rem(32) rem(8) rem(24) rem(8);

      &:before {
        width: 100%;
        height: 1px;
        top: auto;
        bottom: 0;
        margin-bottom: -1px;
      }

      &:hover,
      &:focus {
        background: transparent;

      }

      &:active,
      .js-enabled &:not(.collapsed) {

        &:before {
          height: 2px;
        }
      }
    }

  }
}


// #region tab panel
// Tab Panel
.tabpanel {
  background: var(--bg-colour);
  height: auto!important;
  display: none!important;  
  padding-top: rem(24);
  position: relative;

  &:focus {
    outline: none;
  }

  .img--banner {
    margin-top: -#{rem(24)};
  }

  &.show {
    display: block!important;
    overflow: visible; // Fix img banners negative margin display problems
  }

  body:not(.js-enabled) &:target {
    display: block!important;
  }
}

// #endregion

.tabs--content {
  display: none;

  & ~ .tabpanel {
    // Fixes the issue of anchor links displaying in weird places
    padding-top: 3rem;
    margin-top: -3rem; 
  }
}


// #region Tabs Carousel
.tabs__container--carousel {
  padding: 0;
  margin-bottom: 0!important;
}

.tabs--carousel {
  position: absolute;
  width: 50%;
  left: 25%;
  z-index: 2;
  padding: rem(24) 0;

  @include media-breakpoint-up(md) {
    width: 50%;
    left: 50%;
    top: rem(64);
    padding: 0 0 0 $container-md-padding;
  }

  ul {
    display:block;
    border: none;
    text-align: center;

    @include media-breakpoint-up(md) {
      text-align: left;
    }

    .tab {
      text-align: center;
      display: inline-block;
      min-width: auto;
      max-width: auto;
      margin: 0 rem(16) 0 0;

      .tab__target {
        display: block;
        width: rem(12);
        height: rem(12);
        border-radius: 50%;
        border: solid 1px $colour-muted;
        padding: 0;

        &:not(.collapsed) {
          @include var(border-color, --colour-primary);
          @include var(background, --colour-primary);

          &:before {
            display: none;
          }
        }

        &:hover {
          @include var(border-color, --colour-primary);

          &:before {
            display: none;
          }
        }
      }
    }
  }
}

.carousel-wrapper {

  position: relative;
  overflow: hidden;
  min-height: rem(400);

  @include media-breakpoint-up(md) {
    min-height: rem(640);
    max-height: rem(640);
  }

  .tabpanel {
    padding: 0;
    background: transparent;
    transition: none!important;
    -webkit-transition-duration: 1.5s!important;
    transition-duration: 1.5s!important;

    > .container {
      animation-duration: 1.25s!important;
    }
  }

  .tabpanel.collapsing {
    display: block!important;
  }

  .tabpanel.opening {
    
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
  }

  @if $compatible == 'true' {
      .tabpanel {
        -webkit-transition-duration: 0s!important;
        transition-duration: 0s!important;

        > .container {
          animation-duration: 0s!important;
        }
      }
  }
  @else {
      .tabpanel.closing ~ .tabpanel.opening > .container {
        @include var(animation, --animation-slide-left);
      }

      .tabpanel.closing > .container {

        @include var(animation, --animation-slide-right-out);
      }
  

      // Move right 
      .tabpanel.opening > .container {
        @include var(animation, --animation-slide-right);

        > .row > .col-12 + .col-12 {
          opacity: 0;
          animation: 1.25s ease-out fade-in 0.25s forwards;
        }
      }
      .tabpanel.opening ~ .tabpanel.closing > .container {

        @include var(animation, --animation-slide-left-out);
      }
  }

  .tabpanel.closing,
  .tabpanel.opening {
    > .btn {
      pointer-events: none;
    }
  }
  .tabpanel.closing {
    > .btn {
      display: none;
    }
  }

}

.tabpanel {

  .btn-icon-only {
    top: 0;
    z-index: 2;
    margin-bottom: 0;
    position: absolute;

    &.tab-left {
      left: 0;
    }

    &.tab-right {
      right: 0;
      left: auto;
    }

    @include media-breakpoint-up(md) {
      top: 50%;
      margin-top: -#{rem(36)};
    }
  }

  .container--split [class^="col-"]:last-of-type > *:first-child {
    padding-top: rem(32);
  }
}

// Non-js fix
body:not(.js-enabled) {
  .carousel-wrapper .tabpanel:last-child {
    display: block!important;
  }
  .carousel-wrapper .tabpanel:target ~ .tabpanel:last-child {
    display: none!important;
  }
}

// #endregion
