@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

// #region alert
.alert-msg {
  --bg-colour: var(--colour-light);
  margin-bottom: rem(48);
  padding-top: rem(16);
  min-height: rem(80);
  position: relative;
  @include var(background,--colour-light);
  text-align: left!important;
  padding-left: rem(72);
  padding-right: rem(24);
  position: relative;
  margin-left: -#{$container-padding};
  margin-right: -#{$container-padding};

  .alert__title{
    padding-bottom: rem(16);
  }

  p {
    margin-left: 0;
  }

  > :first-child {
    max-width: calc(100% - #{rem(48 + 16)});
  }
  

  > :last-child:not(.btn) {
    padding-bottom: rem(32);
  }


  @include media-breakpoint-up(sm) {
    padding-top: rem(24);
    padding-left: rem(104);
    min-height: rem(104);
    margin-left: 0;
    margin-right: 0;
  }
  
  > .strip {
    width: rem(48);
    @include var(background,--colour-alert-highlight);
    padding: rem(20) rem(12);
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;

    @include media-breakpoint-up(sm) {
      width: rem(80);
      padding: rem(32) rem(24) ;
    }
  }

  > .strip .svg__wrapper {
    display: block;
    position: absolute;

    svg {
      width: rem(24);
      height: rem(24);

      @include media-breakpoint-up(sm) {
        width: rem(32);
        height: rem(32);

      }
    }
  }
}

// #endregion

// #region Alert Danger
.alert--danger{

  --bg-colour: var(--colour-alert-error-highlight);
  @include var(background,--colour-alert-error-highlight);

  > .strip {
    @include var(background ,--colour-alert-error);
  }

  > .svg__wrapper svg {
    @include var(stroke,--colour-danger);
  }
  > .svg__wrapper--fill svg {
    stroke: none;
    @include var(fill,--colour-danger);
  }
}
// #endregion 

// #region Alert Warning
.alert--warning{
  --bg-colour: var(--colour-alert-warning);
  @include var(background,--colour-alert-warning);

  > .strip {
    @include var(background ,--colour-alert-warning-highlight);
  }

  > .svg__wrapper svg {
    @include var(stroke, --colour-warning);
  }
  > .svg__wrapper--fill svg {
    stroke: none;
    @include var(fill,--colour-warning);
  }
}
// #endregion

// #region Alert Success
.alert--success{
  
  --bg-colour: var(--colour-alert-success);
  @include var(background ,--colour-alert-success);

  > .strip {
    @include var(background ,--colour-alert-success-highlight);
  }

  > .svg__wrapper svg {
    @include var(stroke, --colour-success);
  }
  > .svg__wrapper--fill svg {
    stroke: none;
    @include var(fill, --colour-success);
  }
}
// #endregion

// #region Alert slim
.alert--slim {
  
  padding: rem(12) rem(16) rem(12) rem(16 + 48);
  min-height: rem(48);

  @include media-breakpoint-up(sm) {
    padding: rem(18) rem(24) rem(18) rem(24 + 64);
    min-height: rem(64);
  }

  p {
    font-weight: bold;
    font-variation-settings: 'wght' 500;
  }

  > :last-child:not(.btn) {
    padding-bottom: 0;
  }

  > .strip {
    padding: rem(12);

    @include media-breakpoint-up(sm) {
      width: rem(64);
      padding: rem(16) ;
    }
  }
}
// #endregion