@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.content-list {
  

  .content-list__title {
    border-bottom: 1px solid currentColor;
    @include var(border-color, --colour-border);


    @include media-breakpoint-up(md) {
      border-bottom: none;
    }
  }

  .list-links {
    max-width: none;

    > li:first-child > a {
      border-top: none;
    }
  }

  .list-links li {
    padding-right: $gutter;
    padding-left: $gutter;

    a[data-toggle] svg {
      transition: transform 0.5s;
    }
  }

  .js-enabled & .list-links li {

    a[data-toggle]:not(.collapsed){
      pointer-events: none;
      svg {
        transform: rotate(90deg);
      }
    }
  }

  
  @include media-breakpoint-up(md) {

    [class*="col-md-"] & {

      padding: 0;
      margin: 0;
      background: none!important;

      .content-list__title {

        border-bottom: 1px solid currentColor;
        @include var(border-color, --colour-border);
      }

      .list-links li {
        min-width: 100%;
      }
    }
  }
}

.content-list + .content-list {

  padding-top: rem(16);
}