@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

// #region Card deck Articles
.article-deck {

  justify-content: start;
  padding-bottom: rem(16);

  // Basics
  @include media-breakpoint-up(sm) {
    margin-left: rem(-16);
    margin-right: rem(-16);
    display: flex;
    flex-flow: row wrap;
    justify-content: flex-start;
  }


  .card {
    //box-shadow: none!important;

    @include media-breakpoint-up(sm) {
      margin: 0 rem(16) rem(32) rem(16);
      width: calc(50% - #{rem(16 + 16)});
    }
    @include media-breakpoint-up(md) {


      width: calc(33.33333% - #{rem(32)});
    }

    .card-title {
      font-size: rem(24);
      letter-spacing: -0.25px;
      line-height: rem(32);
      padding-bottom: rem(24);
      color: inherit;
    }

    .card-icon,
    .card-footer .btn-link {
      display: none;
    }

    .card-body {
      padding: rem(16) rem(16) 0 rem(16);

      @include media-breakpoint-up(md) {
        padding-top: rem(24);
      }
    }

    .card-footer {
      padding: rem(0) rem(16) 0 rem(16);

      .card-author {
        display: block;
        padding: 0;
      }
    }
    
    .card-image {
      overflow: hidden;

      img {
        transition: 0.25s;
      }
    }

    &:hover:not(:active),
    &:focus:not(:active),
    &:active {
      
      .card-image img{
        
        transform: translate(-50%, -50%) scale(1.1);
      }
  
      .card-body .card-title {
        text-decoration: underline;
      }
    }
  }
}
// #endregion

// #region Square
.article-deck--square {

  .card-image {
    padding-top: 100%;
  }
}
// #endregion

// #region Square
.article-deck--images-only {

  .card {
    padding: 0;
  }
  
  .card-body,
  .card-footer {
    display: none;
  }
}
// #endregion

// #region Image left
.article-deck--left {
  
  @include media-breakpoint-up(sm) {

    
    margin-left: 0;
    margin-left: 0;
    
    .card:not(:first-child),
    &:not(.article-deck--fixed) .card:first-child {
      display: flex;
      flex-flow: row nowrap;
      padding: 0;
      margin: 0 0 rem(16) 0;
      width: 100%;

      .card-image {
        width: rem(186);
        height: rem(186);
        padding: 0;
        flex-shrink: 0;
      }

      .card-body > *:not(.card-title){
        display: none;
      }

      .card-title{
        display: -webkit-box;
        -webkit-line-clamp: 3;
        -webkit-box-orient: vertical;
        max-height: rem(32 * 3);
        overflow: hidden;
        padding: 0;
        margin-bottom: rem(24);
      }

      .card-footer {
        position: absolute;
        bottom: rem(24);
        left: rem(186);
        width: calc(100% - #{rem(186)});
      }
    }
  }

  @include media-breakpoint-up(md) {
    .card:not(:first-child),
    &:not(.article-deck--fixed) .card:first-child {
      margin: 0 rem(16) rem(32) rem(16);
      width: calc(50% - #{rem(16 + 16)});
    }
  }
}
// #endregion

// #region Fixed
.article-deck--fixed {

  // Make sure 4 are only ever shown
  .card:nth-child(4) ~ .card {
    display: none;
  }

  @include media-breakpoint-up(sm) {

    .card:first-child {
      margin-left: 0;
      margin-right: 0;
      margin-bottom: rem(16);
      width: 100%;
    }
  }

  @include media-breakpoint-up(md) {
    display: block;
    overflow: hidden;

    .card {
      width: calc(50% - #{rem(8)});
      float: right;
    }

    .card:first-child {
      float: left;
      display: inline-block;
      width: calc(50% - #{rem(8)});
    }
  }

  @supports (display: grid) {
    // Change the layout
    @include media-breakpoint-up(md) {
      display: grid;
      overflow: visible;
      grid-template-columns: calc(50% - 0.5rem) calc(50% - 0.5rem);
      grid-gap: 0;
      column-gap: rem(16);
      margin-left: 0;
      margin-right: 0;
      margin-bottom: rem(32);

      .card {
        width: auto!important;
        margin: 0 0 rem(16) 0!important;
        grid-column: 2;
      }

      .card:first-child {
        grid-column: 1;
        grid-row: 1 / 4;
        display: flex;
      }

      .card:first-child,
      .card:nth-child(4) {

        margin: 0!important;
      }
    }
  }
}
// #endregion

// #region Feed
.article-deck--feed {

  justify-content: flex-start;

  body:not(.ie) & {
    align-items: flex-start;
  }
  
  .card {
    @include media-breakpoint-up(md) {
      width: calc(33.33333% - #{rem(32)});
    }

    .card-image {
      padding: 0;

      img {
        position: static;
        transform: none;
      }
    }

    &.js-trans {
      transform: translate(0, $animation_offset);
      opacity: 0;
      @include var(animation, --animation-fade-up);
    }
    
    &.js-hide .card-image{
      display: none;
    }

    &:hover:not(:active),
    &:focus:not(:active),
    &:active {
      
      .card-image img{
        
        transform: scale(1.1);
      }
    }

    .card-body .small {
      padding-top: rem(16 * 2);
      padding-bottom: 0;
      @include var(color,--colour-muted);
    }
  }


  @include media-breakpoint-up(sm) {

    min-height: 2px; // Needed to communicate with JS
  }
  @include media-breakpoint-up(md) {

    min-height: 3px; // Needed to communicate with JS
  }
}

body.js-enabled.ie .article-deck--feed.js-processed {
    height: auto !important;
}

body.js-enabled:not(.ie) .article-deck--feed.js-processed {

  @media (max-width: 36em) {
    // Lets make sure that the mobile height is always automatic
    // and no JS messes up the sizing
    height: auto!important;
  }

  @include media-breakpoint-up(sm) {

    display: flex;
    flex-flow: column wrap;
    align-content: space-between;
    padding-top: 2rem;
    padding-bottom: 0;
    margin-top: -2rem;
    margin-left: rem(-8);
    margin-right: rem(-8);

    // Force new columns 
    &:before,
    &:after {
      content: "";
      flex-basis: 100%;
      width: 0;
      order: 2;
    }

    .card {
      margin-bottom: 0;
      margin-top: rem(16);
      margin-left: rem(8);
      margin-right: rem(8);
      width: calc(50% - #{rem(16)});

      &:nth-child(1),
      &:nth-child(2) {
        margin-top:0;
      }
    }
  
    .card:nth-child(odd) { order: 1; }
    .card:nth-child(even) { order: 3; }
  }

  @include media-breakpoint-up(md) {
    
    margin-left: rem(-16);
    margin-right: rem(-16);

    .card {
      margin-bottom: rem(8);
      margin-top: rem(16);
      margin-left: rem(16);
      margin-right: rem(16);
      width: calc(33.33333% - #{rem(32)});
    }

    &:after {
      content: "";
      flex-basis: 100%;
      width: 0;
      order: 4;
    }

    .card:nth-child(3){
      margin-top:0;
    }

    .card:nth-child(3n+1) { order: 1; }
    .card:nth-child(3n+2) { order: 3; }
    .card:nth-child(3n)   { order: 5; }
  }
}

// #endregion
