@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.img-grid {

  &__item {
    padding-left: rem(64 + 16);
    position: relative;
    display: block;
    width: 100%;
    min-height: rem(64);
    margin-bottom: rem(16);

    @include media-breakpoint-up(sm) {
      text-align: center;
      padding-left: 0;
      margin-bottom: rem(32);
    }
  }

  a.img-grid__item {

    text-decoration: none!important;

    &:hover:not(:active),
    &:focus:not(:active),
    &:active {

      .img-grid__title {
        text-decoration: underline;
      }

      img {
        opacity: 0.8;
      }

      @include media-breakpoint-up(md) {
          
        .img-grid__img {

          img {
            opacity: 1;
            transform: translate(-50%,-50%) scale(1.1);
          }
        }
      }
    }
  }

  &__img {
    height: rem(64);
    width: rem(64);
    position: absolute;
    top: 0;
    left: 0;
    border: 1px solid currentColor;
    @include var(border-color,--colour-border);
    overflow: hidden;
    
    img {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100%;
      height: 100%;
      min-height: 100%;
      min-width: 100%;
      object-fit: cover;
      transform: translate(-50%,-50%);
      margin:0;
    }

    @include media-breakpoint-up(sm) {
      position: relative;
      height: rem(200);
      width: rem(200);
      display: inline-block;
      margin-bottom: rem(8);
    }
  }

  &__title {
    padding-top: rem(8);
    padding-bottom: rem(8);
    display: block;
  }

  &__desc{
    padding-bottom: rem(8);
    display: block;
  }
}


.img-grid--logos {

  .img-grid__img {

    img {

      width: calc(100% - #{rem(16)});
      height: calc(100% - #{rem(16)});
      min-height: calc(100% - #{rem(16)});
      min-width: calc(100% - #{rem(16)});
      
      @include media-breakpoint-up(sm) {

        width: calc(100% - #{rem(32)});
        height: calc(100% - #{rem(32)});
        min-height: calc(100% - #{rem(32)});
        min-width: calc(100% - #{rem(32)});
      }
    }

  }
}