@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.accordion {
  margin-bottom: rem(64);
  border-bottom: 1px solid;
  @include var(border-bottom-color, --colour-border);
  width: 100%;
  max-width: $max-content-width;
  
  .collapse,
  .collapsing {
    padding: 0;
  }

  // Make sure text is also left algined
  // By removing the margin left on text elements
  text-align:left;
}

.accordion--small{
  max-width: $max-content-width;
}

.accordion__tab {
  border-top: 1px solid currentColor;
  @include var(border-top-color, --colour-border);
  display: block!important;
}

.accordion .item + .h2 {

  border-top: 2px solid currentColor;
  @include var(border-top-color, --colour-border);

  padding-top: rem(48) !important;
}

.accordion .item > .collapse,
.accordion .item > .collapsing {
  padding-right: rem(48)!important;
  font-size: rem(16);
  line-height: rem(14);
  @include var(color, --colour-body);
  
}

.accordion__title {
  padding: 0;
  font-size: rem(16);
  line-height: rem(24);
  display: block;
  padding-top: rem(24);
  padding-bottom: rem(24);
  text-decoration: none;
  position: relative;
  padding-right: rem(48);
  font-weight: bold;
  @include var(font-family, --font-heading);

  &:hover,
  &.hover,
  &:not(.collapsed){
    @include var(color, --colour-primary);
  }

  //@extend %plus-icon;
  @include plus-icon();

  &:after {

    top: rem(20);
  }

  &:after {

    content: "";
    position: absolute;
    top: calc(50% - #{rem(32/2)});
    left: auto;
    right: rem(8);
    height: rem(32);
    width: rem(32);
    transition: transform 0.25s ease-out;

    @if $compatible == 'true' {
      background-image: escape-svg($plus-red-svg);
    }
    @else {

      background-color: var(--colour-primary);
    }
  }


}
