@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.filter {
  border-bottom: 1px solid currentColor;
  @include var(border-color,--colour-border);

  padding: rem(24) rem(16) rem(0) rem(16);

  > .row {
    position: relative;
    padding-left: rem(40);

    > .svg__wrapper {
      position: absolute;
      top: 0;
      left: 0;
    }
  }
  
  .filter__title .h6 {
    padding-bottom: rem(16);
  }

  .filter__btn .btn {
    margin-bottom: calc(#{rem(24)} - 1px);
  }

  @include media-breakpoint-up(sm) {
    
    padding: rem(28) rem(48) rem(28 - 24) rem(48);

  }

  @include media-breakpoint-up(md) {
    
    padding-left: #{$container-md-padding};
    padding-right: #{$container-md-padding};

    .filter__btn {
      text-align: right;
    }
  }

  // Form contents 
  .form-group {

    @include media-breakpoint-up(md) {

      display: flex;
      flex-flow: row wrap;
      margin-right: rem(-8);
      margin-left: rem(-8);

      .custom-control {

        flex: 0 0 50%;
        padding-right: rem(8);
        padding-left: rem(8);
      }
    }

    @include media-breakpoint-up(md) {

      .custom-control {

        flex: 0 0 33.3333%;
      }
    }
  }
}
