@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";

.progress__container {
  padding: rem(16) rem(16) rem(24) rem(16);
  border-bottom: 1px solid currentColor;
  @include var(border-color,--colour-border);

  @include media-breakpoint-up(sm) {
    
    padding: rem(24) rem(24) rem(32) rem(24);

  }

  @include media-breakpoint-up(md) {
    
    padding: rem(32) #{$container-md-padding} rem(32) #{$container-md-padding};
  }

}
.progress {
  display: flex;
  flex-wrap: wrap;
  margin-right: rem(-16);
  margin-left: rem(-16);
  counter-reset: progress;
  position: relative;
  padding-bottom: 0;
  z-index: 1;
  
  @include media-breakpoint-up(sm) {

    max-width: 100%;
    margin: 0;
  }

  li {
    min-height: 1px;
    padding-right: rem(8);
    padding-left: rem(8);

    flex: 1 0 0px;

    text-align: center;
    counter-increment: progress;
  }

  li:first-child:nth-last-child(4),
  li:first-child:nth-last-child(4) ~ li {
    max-width: 25%;
  }

  li:first-child:nth-last-child(5),
  li:first-child:nth-last-child(5) ~ li {
    max-width: 20%;
  }

  &__step-number {
    display: inline-block;
    font-size: rem(12);
    font-weight: 700;
    font-variation-settings: 'wght' 600;
    height: rem(24);
    line-height: rem(24);
    width: rem(24);

    @include media-breakpoint-up(sm) {

      font-size: rem(16);
      height: rem(32);
      line-height: rem(32);
      width: rem(32);
      margin-bottom: rem(8);
    }
    border: 1px solid currentColor;
    @include var(color,--colour-muted);
    @include var(border-color,--colour-border);

    border-radius: 50%;
    position: relative;
    overflow: hidden;
    text-indent: -500%;
    padding: 0;

    &:before {
      content: counter(progress);
      position: absolute;
      width: 100%;
      text-align: center;
      text-indent: 0;
      top: 0;
      left: 0;

      @if $compatible == 'true' {
      line-height: em(20); 
      }
      @else {
        line-height: rem(22);
      }


      @include media-breakpoint-up(sm) {
        @if $compatible == 'true' {
          line-height: em(28); 
          }
          @else {
            line-height: rem(30);
          }
      }
    }

    [data-status="done"] &,
    [data-status="current"] & {
      @include var(background,--colour-primary);
      @include var(border-color,--colour-primary);
      @include var(color,--colour-white);
      font-weight: 900;
      font-variation-settings: 'wght' 700;
    }

    [data-status="done"] &:before {
      content: "";
      background-image: escape-svg($tick-white-svg);
      background-size: rem(16) rem(16);

      @include media-breakpoint-up(sm) {

      background-size: rem(24) rem(24);

      }
      background-position: 50% 50%;
      background-repeat: no-repeat;
      height: 100%;
    }
  }

  &__step-title {
    display: none;
    @include var(color,--colour-body-light);
    padding-bottom: 0;


    &.current {
      @include var(color,--colour-body-highlight);
      font-weight: 700;
    }

    @include media-breakpoint-up(sm) {

      display: block;
    }
  }


  &__step-status {
    
    display: block;
    height: 2px;
    width: calc(100% - #{rem(24)} - 8px);
    min-width: calc(100% - #{rem(24)} - 8px);
    @include var(background-color,--colour-border);
    position: absolute;
    top: calc(#{rem(12)} - 1px);
    left: calc(50% + #{rem(12)} + 4px);
    padding:0;
    z-index: -2;
    overflow: hidden;


    @include media-breakpoint-up(sm) {

      width: calc(100% - #{rem(32)} - 8px);
      min-width: calc(100% - #{rem(32)} - 8px);
      top: calc(#{rem(16)} - 1px);
      left: calc(50% + #{rem(16)} + 4px);
    }

    [data-status="done"] & {
      
      &:after {
        width: 100%;
      }
    }

    &:after {
      content: "";
      display: block;
      height: 4px;
      @include var(width,--progress);
      @include var(background-color,--colour-primary);
      position: absolute;
      top: 0;
      left: 0;
      z-index: -1;
    }
  }


  li:last-child .progress__step-status {
    display: none;
  }

  &__current {
    text-align: center;
    padding-bottom: 0;
    display: block;
    font-weight: 900;
    
    @include media-breakpoint-up(sm) {

      display: none;
    }
  }
}