@charset "UTF-8";
$compatible: 'false' !default;
$mobile: 'false';
$system: 'false';
$print: 'false' !default;
@import "../../functions";


.detail-list {
  
  .table__wrapper {
    margin-bottom: rem(16);
  }
  table {
  
    th {
      font-weight: normal;
      font-variation-settings: 'wght' 300;
    } 
    
    td {
      font-weight: bold;
      font-variation-settings: 'wght' 600;
    }
  }
  &:not(.detail-list--grouped) table {

    tbody:first-child tr:first-child{

      border-top-width: 1px;
    }

    tr:last-child {
      border-bottom: none;
    }

    th, td {
      padding: rem(32) 0;
      width: 100%;
      display: block;

      > *:last-child {
        margin-bottom: 0;
      }
    }

    th {
      padding-bottom: rem(8);
    }

    td {
      padding-top: 0;
    }
  }

  @include media-breakpoint-up(sm) {

    .table__wrapper {
      margin-bottom: rem(48);
    }
    &:not(.detail-list--grouped) table {
  
      th, td {
        padding: rem(32) 0;
        display: table-cell;
  
        > *:last-child {
          margin-bottom: 0;
        }
      }
  
      th {
        width: 33%;
      }

      td {
        padding-left: rem(16);
      }

      td + td {
        text-align: right;
        width: 0%;
      }
    }
  }

  @include media-breakpoint-up(md) {
    display: flex;


    h2 {
      max-width: rem(320);
      padding-right: rem(24);
      margin-bottom: rem(64);
    }
    .table__wrapper {
      padding-right: 0;
      margin-bottom: rem(64);
    }
    table {

      th {
        width: 25%;
      }
    }
    
    &:not(.detail-list--grouped) table {

      th {
        width: 25%;
      }

      td {
        padding-left: rem(16);
      }
    }
  }
}

.detail-list + .detail-list {
  padding-top: rem(48);
  border-top: 2px solid currentColor;
  @include var(border-color, --colour-border);
}

.detail-list--grouped {

  border-top: 2px solid currentColor;
  border-bottom: 2px solid currentColor;
  @include var(border-color, --colour-border);
  padding-top: rem(48);
  margin-bottom: rem(48);

  table {
    border: none;

    tbody:first-child tr:first-child,
    tr {
      border: none;
    }

    th {
      padding-left: 0;
    }
  }


  @include media-breakpoint-up(sm) {

    display: flex;
    align-items: flex-start;
    flex-wrap: wrap;
  
    h2 {
      width: 100%;
    }

    .table__wrapper {
      width: auto;
      flex-grow: 1;
    }

    table {

      th {
        width: 33%;
      }
    }

  }

  @include media-breakpoint-up(md) {

    

    table {

      th {
        width: 25%;
      }
    }

  }
}


.detail-list--grouped + .detail-list--grouped {
  padding-top: 0;
  border-top: 0;
}